<template>
  <div :style="'overflow-y:auto;height:'+ containerHeight+'px'" >
    <div class="info-body">
      <Card :bordered="false" dis-hover class="member-card" style="background-color: #f0f0f0;">
        <Row style="font-size: 14px;">
          <i-col span="6" >
            <span>名称</span>
          </i-col>
          <i-col span="6" >
            <span>用法</span>
          </i-col>
          <i-col span="12">
            <span>说明</span>
          </i-col>
        </Row>
      </Card>
      <Card :bordered="false" dis-hover class="member-card">
        <Row>
          <i-col span="6" >
            WORKSPACE
          </i-col>
          <i-col span="6" >
            ${WORKSPACE}
          </i-col>
          <i-col span="12">
            代表当前任务在Agent上的工作目录
          </i-col>
        </Row>
      </Card>
      <Card :bordered="false" dis-hover class="member-card">
        <Row>
          <i-col span="6" >
            BUILDTOOL
          </i-col>
          <i-col span="6" >
            ${BUILDTOOL}
          </i-col>
          <i-col span="12">
            Agent上的构建工具目录
          </i-col>
        </Row>
      </Card>
      <Card :bordered="false" dis-hover class="member-card">
        <Row>
          <i-col span="6" >
            BUILDVERSION
          </i-col>
          <i-col span="6" >
            ${BUILDVERSION}
          </i-col>
          <i-col span="12">
            代表当前任务的构建版本，根据构建次数自增
          </i-col>
        </Row>
      </Card>
      <Card :bordered="false" dis-hover class="member-card">
        <Row>
          <i-col span="6" >
            COMMONSPACE
          </i-col>
          <i-col span="6" >
            ${COMMONSPACE}
          </i-col>
          <i-col span="12">
            各构建任务可以共享的一个目录
          </i-col>
        </Row>
      </Card>

      <Card :bordered="false" dis-hover class="member-card">
        <Row>
          <i-col span="6" >
            BUILDBACKUPSPACE
          </i-col>
          <i-col span="6" >
            ${BUILDBACKUPSPACE}
          </i-col>
          <i-col span="12">
            根据构建版本自动创建的目录，不会随任务执行清除
          </i-col>
        </Row>
      </Card>

      <Card :bordered="false" dis-hover class="member-card">
        <Row>
          <i-col span="6" >
            TASKID
          </i-col>
          <i-col span="6" >
            ${TASKID}
          </i-col>
          <i-col span="12">
            构建任务ID
          </i-col>
        </Row>
      </Card>

      <Card :bordered="false" dis-hover class="member-card">
        <Row>
          <i-col span="6" >
            TASKNAME
          </i-col>
          <i-col span="6" >
            ${TASKNAME}
          </i-col>
          <i-col span="12">
            构建任务名称
          </i-col>
        </Row>
      </Card>

      <Card :bordered="false" dis-hover class="member-card">
        <Row>
          <i-col span="6" >
            HISTORYID
          </i-col>
          <i-col span="6" >
            ${HISTORYID}
          </i-col>
          <i-col span="12">
            构建任务记录ID,使用记录相关API获取上传package下载URL
          </i-col>
        </Row>
      </Card>
    </div>
  </div>
</template>

<script>
  import { mapGetters, mapMutations } from 'vuex'


  export default {
    name: 'ProjectInfo',
    props: ['project'],
    data () {
      return {
      }
    },
    computed: {
      ...mapGetters(['appBodyHeight','userInfo']),
      ...mapGetters('projectglobal',['createDocumentType']),
      ...mapGetters('document',['breadNav']),
      containerHeight: function () {
        return this.appBodyHeight
      }

    },

    methods: {
      ...mapMutations('projectglobal',['setCreateDocumentType','setCreateDialogShow']),

    },

    created: function() {
      this.$axios.get('/api/common/dicconfig/5/dicconfigs').then(response => {
        this.platforms=response.data.result
      }, response => {
        // error callback
      })

      this.$axios.get('/api/common/users/list').then(response => {
        this.userList=response.data.result
      }, response => {
        // error callback
      })

      this.$axios.get('/api/project/products').then(response => {
        this.products=response.data.result
      }, response => {
        // error callback
      })

      this.projectInfo = this.project
    },

    mounted: function () {
    },

    watch: {

      project: function () {
        this.projectInfo = this.project
      }

    },

    components: {
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="less">

.info-body {
  width: 95%;
  padding-top:16px;
  margin-left: auto;
  margin-right: auto;
}

.member-card {
  width: 100%;
  height: 45px;
  margin-right: 16px;
  margin-left: 16px;
  margin-bottom: 2px;
}

</style>



